<template>
    <div>
        <div class="box">
            <div class="search-box clearfix" ref="search">
                <div class="search fl">
                    <van-search placeholder="请输入搜索关键词"  />
                </div>
                <div class="relase fr">
                    <span><van-icon name="add-o" size=".8rem"/></span>
                    <span>发布</span>
                </div>

            </div>

            <div class="tabs-list-box" ref="tabslistbox" >
                <!-- <van-tabs >
                    <van-tab v-for="(item,index) in tabsData.data" :key="index" :title="item.value" @change="tabs(item.key)" >
                        内容 {{ index }}
                    </van-tab>
                </van-tabs> -->
                <van-tabs @click="onClick">
                    <van-tab v-for="(item,index) in tabsData" :key="index" :title="item.name" >
                        
                        <div class="video-content" ref="videoContent" @scroll="load($event)">
                            <div class="video-box" ref="videoBox" v-for="(item,index) in data" :key="index">

                                <div class="v-content" ref="vContent" v-for="(item,index1) in endData" :key="index1">

                                    <div class="play" @click="play(index1)"><van-icon name="play-circle-o" size="60px" color="#fff" v-show="item.isPlay" /></div>
                                    <div class="pause" @click="play(index1)"><van-icon name="pause-circle-o" size="60px" color="#fff" v-show="!item.isPlay" /></div>

                                    <video ref="player" class="video" :src="item.data.playUrl"></video> 

                                    <div class="title">{{item.data.title}}</div>
                                    
                                    <!-- <img :src="item.data.header.icon" alt=""> -->
                                    <div class="play-numver"></div>

                                    <div class="time">{{item.blackText}}</div>
                                </div>
                                    <!-- <p class="tip-text" v-if="isHas">没有更多数据可加载了</p> -->
                            </div>

                        </div>                       
                    </van-tab>
                </van-tabs>
            </div>
        </div>
        <div>
             <router-view />
        </div>
    </div>
</template>

<script>

import $ from 'jquery'
import Axios from 'axios'

    export default {
        name:'watermelonVideo',

        data(){
            return {

                tabsData:[],

                data:[],

                index:'0',

                key:14,  

                endData:[],

                isHas:true

                // isPlay:true
                
                   
            }
        },

        created(){
            let _this =this
            //请求数据
            this.axios({
                //请求类型
                method:'GET',
                //请求地址
                url:'https://api.apiopen.top/videoHomeTab'
                
            }).then(result =>{
                // console.log('result111111==>',result)
                _this.tabsData = result.data.result;

                // 因前四个没有数据，所以从第四个开始
                _this.tabsData = _this.tabsData.slice(4)       
            }).catch(err => {
                // console.log('err ==> ', err);
            })


            // 广告的内容
            //第一个请求数据
            this.axios({
                //请求类型
                method:'GET',
                //请求地址
                url:'https://api.apiopen.top/videoCategoryDetails?id=' + _this.key
            }).then(result =>{
                _this.data = result.data.result;

                //截取一个
                _this.data =  _this.data.slice(1,2)
                // console.log('_this.data11111 ==>',_this.data)           
            }).catch(err => {
                // console.log('err ==> ', err);
            }) 


            this.$toast.loading({
                duration: 0,
                message: "加载中..."
            });
            this.axios({
                //请求类型
                method:'GET',
                //请求地址
                url:'https://api.apiopen.top/videoRecommend?id=180634'
            }).then(result =>{
                _this.endData = result.data.result
                _this.endData = _this.endData.slice(1)
                // _this.endData = _this.endData.slice(0,3)

                // console.log('enddata==>',_this.endData)
                // 增加isPlay属性
                for(var i=0;i<_this.endData.length;i++){
                    _this.endData[i].isPlay = true
                }

                 //取消加载提示
                 this.$toast.clear();
            }).catch(err => {
                //取消加载提示
                this.$toast.clear();
            })
            
            
        },

        mounted(){

            // 广告的内容
            let _this = this;
            // console.log('2222222222222',this.data)
            // this.axios({
            //     //请求类型
            //     method:'GET',
            //     //请求地址
            //     url:'https://api.apiopen.top/videoRecommend?id=180634'
            // }).then(result =>{
            //     // console.log('result6666==>',result)
            //     _this.endData = result.data.result
            //     _this.endData = _this.endData.slice(1)
            //     // console.log('enddata==>',_this.endData)
            //     // 增加isPlay属性
            //     for(var i=0;i<_this.endData.length;i++){
            //         _this.endData[i].isPlay = true
            //     }
            // }).catch(err => {
            //     // console.log('err ==> ', err);
            // })


            this.$toast.loading({
                    duration: 1000,
                    message: "加载中..."
            });


            

        },

        methods:{

            // 懒加载
            load(event){
   
                // 顶部搜索的高度
                let height = this.$refs.search.offsetHeight;
                // console.log('height==>',height)

                let tabslistbox = this.$refs.tabslistbox.offsetHeight;
                // console.log('tabslistbox==>',tabslistbox)

                let videoContent = this.$refs.videoContent[0].offsetHeight
                // console.log('videoContent==>',videoContent)

                let videoBox = this.$refs.videoBox[0].offsetHeight;
                // console.log('videoBox==>',videoBox)


                let vContent = this.$refs.vContent[this.$refs.vContent.length -1 ];
                // console.log('vContent==>',vContent)

                let movieItemStyle = vContent.getBoundingClientRect();

                let top = movieItemStyle.top;
                // console.log('top ==>',top)

                let scroll = event.target.scrollTop
                // console.log('scoll==>',scroll)

                let isPass = videoContent - videoBox + 44 + height + 24 >=top
                // console.log('isPass ==>',isPass)

                // if(isPass){

                //     //  this.endData = this.endData.slice(0,3+3)
                //     //  this.endData = this.endData.concat()
                //     // console.log('懒加载触发')
                // }

            },
            
            // 请求数据
            onClick(index){
                let _this = this
               
                 this.$toast.loading({
                    duration: 0,
                    message: "加载中..."
                });

                let key = this.tabsData[index].id 

                //  请求数据
                this.axios({
                    //请求类型
                    method:'GET',
                    //请求地址
                    url:'https://api.apiopen.top/videoCategoryDetails?id=' + key
                }).then(result =>{
                    _this.data = result.data.result;
                   _this.data =  _this.data.slice(1,2)           
                }).catch(err => {
                    // console.log('err ==> ', err);
                })



                // 根据tabs内容请求相应的数据
                let id = this.data[0].id
                // console.log('id ==>',id)
                this.axios({
                    //请求类型
                    method:'GET',
                    //请求地址
                    url:'https://api.apiopen.top/videoRecommend?id=' + id
                }).then(result =>{
                    _this.endData = result.data.result
                    _this.endData = _this.endData.slice(1)

                    // 增加isPlay属性
                    for(var i=0;i<_this.endData.length;i++){
                        _this.endData[i].isPlay = true
                    }
                    _this.endData = _this.endData.concat()

                    //取消加载提示
                    this.$toast.clear();

                }).catch(err => {
                    // console.log('err ==> ', err);
                })

            },


            // 点击播放
            play(index){
                let _this = this;
                let videos = this.$refs.player
                console.log('video==>',videos)

                for(var i=0;i<videos.length;i++){
                    videos[i].pause();        
                }

                videos[index].play();

                if(videos[index].play()){
                   
                    for(let j=0;j<this.endData.length; j++){
                        _this.endData[j].isPlay = true;
                    }
                    this.endData[index].isPlay = false;
                    this.endData = this.endData.concat()
                }
           
            },
        }   
    }
</script>

<style lang="less" scoped>
    .search-box{
        width:calc(100% - .6rem);
        height: 1.5rem;
        background: #e498bc;
        margin-top: 24px;
        padding:0 .3rem;

        .search{
            width:calc(100% - 1.2rem);
            margin-top: .15rem;
        }

        .van-search {
            padding: 0;
            height: 1.2rem;
            border-radius: .2rem;          
        }
        .van-search .van-cell {
                padding: 0;
                background: #fff;
        }
        .van-icon{
            height: .3rem;
        }
        .relase{
            width: .8rem;
            height: 1.2rem;
            margin-top: .15rem;
            color: #fff;
            font-size: .3rem;
            text-align: center;    
        }
    }

    .tabs-list-box{
        .video-content{
            height: 13.1rem;
            overflow: hidden;
            overflow-y: auto;
        }
        .van-tab{
            height: 1rem;
            color: #000;
            line-height:1rem;
            flex-basis: .3rem;
            width: 1rem !important;
           
        }
        .van-tab--active{
            color: #e498bc;
        }
        .van-tabs__line{
            height: 0px !important;
        }
        .van-tabs__wrap--scrollable .van-tab {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 10%;
        flex: 0 0 10%;
        }

        .video-box{
            width: 100%;
            height: 6rem;
            background: #ddd;

            >div{
                width: 100%;
                height: 100%;
                background: #f5f5f5;
                margin-bottom: .2rem;
                position: relative;
            } 
            .video{
                width: 100%;
                height: 100%;
            }

            .title{
                width: calc(100% - .6rem);
                height:.9rem;
                padding: .3rem;
                font-size: .5rem;
                color: #ddd;
                line-height: .6rem;
                overflow: hidden;
                -webkit-line-clamp:2;
                -webkit-box-orient: vertical;
                text-overflow: ellipsis;
                z-index: 10;
                position: absolute;
                top: .3rem;
                left: 0;
                text-align: center
            }

            .play-numver{
                position: absolute;
                left: .3rem;
                bottom: .4rem;
                max-width: 3rem;
                height: .5rem;
                font-size: .4rem;
                color: #fff;
            }

            .time{
                position: absolute;
                right: .6rem;
                bottom: .4rem;
                max-width: 3rem;
                height: .5rem;
                font-size: .4rem;
                color: #fff; 
                background: rgba(0, 0, 0, 0.3);
                border-radius: .2rem;
                padding: 0 .2rem;
            }

            .play{
                width: 60px;
                height: 60px;
                position: absolute;
                top:50%;
                left:50%;
                right: 0;
                bottom: 0;
                margin-top: -30px;
                margin-left: -30px;
                z-index: 20;

            }
            .pause{
                width: 60px;
                height: 60px;
                position: absolute;
                top:50%;
                left:50%;
                right: 0;
                bottom: 0;
                margin-top: -30px;
                margin-left: -30px;
                 z-index: 20;
            }
        }

        .tip-text{
            text-align: center;
        }
    }

</style>